'use client';

import { Product } from '@/types/indian-product';
import { IndianProductCard } from './indian-product-card';
import { Card, CardContent } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';

interface IndianProductGridProps {
  products: Product[];
  selectedProducts: string[];
  onToggleSelection: (productId: string) => void;
  onViewDetails?: (product: Product) => void;
  isLoading?: boolean;
  currency: 'INR' | 'USD';
  convertPrice: (price: number) => number;
  getCurrencySymbol: () => string;
}

export function IndianProductGrid({
  products,
  selectedProducts,
  onToggleSelection,
  onViewDetails,
  isLoading = false,
  currency,
  convertPrice,
  getCurrencySymbol
}: IndianProductGridProps) {
  if (isLoading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
        {Array.from({ length: 8 }).map((_, index) => (
          <Card key={index}>
            <CardContent className="p-6">
              <div className="space-y-4">
                <Skeleton className="h-48 w-full rounded-lg" />
                <div className="space-y-2">
                  <Skeleton className="h-4 w-3/4" />
                  <div className="flex gap-2">
                    <Skeleton className="h-5 w-16" />
                    <Skeleton className="h-5 w-20" />
                  </div>
                </div>
                <Skeleton className="h-8 w-24" />
                <Skeleton className="h-16 w-full" />
                <div className="space-y-2">
                  <Skeleton className="h-4 w-full" />
                  <Skeleton className="h-3 w-2/3" />
                </div>
                <div className="flex gap-2">
                  <Skeleton className="h-8 flex-1" />
                  <Skeleton className="h-8 flex-1" />
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    );
  }

  if (products.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center py-16 px-4">
        <div className="text-center space-y-4">
          <div className="w-24 h-24 mx-auto bg-muted rounded-full flex items-center justify-center">
            <span className="text-4xl text-muted-foreground">🔍</span>
          </div>
          <h3 className="text-xl font-semibold">No products found</h3>
          <p className="text-muted-foreground max-w-md">
            We couldn't find any products matching your search criteria in Indian cities. 
            Try adjusting your filters or search terms.
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
        {products.map((product) => (
          <IndianProductCard
            key={product.id}
            product={product}
            isSelected={selectedProducts.includes(product.id)}
            onToggleSelection={onToggleSelection}
            onViewDetails={onViewDetails}
            currency={currency}
            convertPrice={convertPrice}
            getCurrencySymbol={getCurrencySymbol}
          />
        ))}
      </div>

      {/* Load More Placeholder */}
      {products.length > 0 && products.length % 12 === 0 && (
        <div className="flex justify-center pt-8">
          <Card className="w-full max-w-md">
            <CardContent className="p-6 text-center">
              <p className="text-muted-foreground mb-4">
                Showing {products.length} products
              </p>
              <p className="text-sm text-muted-foreground">
                More products available with different filters
              </p>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
}
