'use client';

import { Product } from '@/types/indian-product';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogClose } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import Image from 'next/image';

interface IndianComparisonToolProps {
  products: Product[];
  isOpen: boolean;
  onClose: () => void;
  onClearSelection: () => void;
  currency: 'INR' | 'USD';
  convertPrice: (price: number) => number;
  getCurrencySymbol: () => string;
}

export function IndianComparisonTool({
  products,
  isOpen,
  onClose,
  onClearSelection,
  currency,
  convertPrice,
  getCurrencySymbol
}: IndianComparisonToolProps) {
  if (products.length < 2) {
    return null;
  }

  const getBestValue = (field: keyof Product, products: Product[]) => {
    switch (field) {
      case 'price':
        return Math.min(...products.map(p => p.price));
      case 'rating':
        return Math.max(...products.map(p => p.rating));
      default:
        return null;
    }
  };

  const isHighlighted = (product: Product, field: keyof Product, bestValue: number | null) => {
    if (bestValue === null) return false;
    
    switch (field) {
      case 'price':
        return product.price === bestValue;
      case 'rating':
        return product.rating === bestValue;
      default:
        return false;
    }
  };

  const bestPrice = getBestValue('price', products);
  const bestRating = getBestValue('rating', products);

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-6xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <div className="flex items-center justify-between">
            <DialogTitle className="text-2xl">Product Comparison</DialogTitle>
            <div className="flex gap-2">
              <Button
                variant="outline"
                size="sm"
                onClick={onClearSelection}
              >
                Clear Selection
              </Button>
              <DialogClose asChild>
                <Button variant="ghost" size="sm">
                  Close
                </Button>
              </DialogClose>
            </div>
          </div>
        </DialogHeader>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mt-6">
          {products.map((product) => (
            <Card key={product.id} className="h-fit">
              <CardHeader>
                <div className="space-y-2">
                  <CardTitle className="text-lg">{product.name}</CardTitle>
                  <div className="flex gap-2">
                    <Badge variant="secondary">
                      {product.category.charAt(0).toUpperCase() + product.category.slice(1)}
                    </Badge>
                    {product.isBestDeal && (
                      <Badge className="bg-primary">Best Deal</Badge>
                    )}
                  </div>
                </div>
              </CardHeader>

              <CardContent className="space-y-4">
                {/* Product Image */}
                {product.image && (
                  <div className="relative w-full h-32 rounded-lg overflow-hidden">
                    <Image
                      src={product.image}
                      alt={product.name}
                      fill
                      className="object-cover"
                    />
                  </div>
                )}

                {/* Price Comparison */}
                <div className="space-y-2">
                  <h4 className="font-medium text-sm">Price</h4>
                  <div className={`p-3 rounded-lg ${
                    isHighlighted(product, 'price', bestPrice) 
                      ? 'bg-green-100 dark:bg-green-900/20 border border-green-200 dark:border-green-800' 
                      : 'bg-muted'
                  }`}>
                    <div className="flex items-center gap-2">
                      <span className="text-2xl font-bold">
                        {getCurrencySymbol()}{convertPrice(product.price).toFixed(0)}
                      </span>
                      {product.originalPrice && (
                        <>
                          <span className="text-sm text-muted-foreground line-through">
                            {getCurrencySymbol()}{convertPrice(product.originalPrice).toFixed(0)}
                          </span>
                          <Badge variant="destructive" className="text-xs">
                            -{discountPercentage}%
                          </Badge>
                        </>
                      )}
                      {isHighlighted(product, 'price', bestPrice) && (
                        <Badge variant="outline" className="text-xs bg-green-50 dark:bg-green-900/50">
                          Lowest Price
                        </Badge>
                      )}
                    </div>
                  </div>
                </div>

                {/* Rating Comparison */}
                <div className="space-y-2">
                  <h4 className="font-medium text-sm">Rating</h4>
                  <div className={`p-3 rounded-lg ${
                    isHighlighted(product, 'rating', bestRating) 
                      ? 'bg-yellow-100 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800' 
                      : 'bg-muted'
                  }`}>
                    <div className="flex items-center gap-2">
                      <span className="text-lg font-semibold">{product.rating}</span>
                      <span className="text-yellow-500">★</span>
                      <span className="text-sm text-muted-foreground">
                        ({product.reviewCount} reviews)
                      </span>
                      {isHighlighted(product, 'rating', bestRating) && (
                        <Badge variant="outline" className="text-xs bg-yellow-50 dark:bg-yellow-900/50">
                          Highest Rated
                        </Badge>
                      )}
                    </div>
                  </div>
                </div>

                <Separator />

                {/* Store Information */}
                <div className="space-y-2">
                  <h4 className="font-medium text-sm">Store Details</h4>
                  <div className="space-y-1 text-sm">
                    <div className="flex justify-between">
                      <span className="text-muted-foreground">Store:</span>
                      <span className="font-medium">{product.store.name}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-muted-foreground">Location:</span>
                      <span>{product.store.city}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-muted-foreground">Distance:</span>
                      <span>{product.store.distance} km</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-muted-foreground">Store Rating:</span>
                      <span>{product.store.rating}★</span>
                    </div>
                  </div>
                </div>

                <Separator />

                {/* Product Details */}
                <div className="space-y-2">
                  <h4 className="font-medium text-sm">Details</h4>
                  <p className="text-sm text-muted-foreground">
                    {product.description}
                  </p>
                  <div className="flex justify-between text-sm">
                    <span className="text-muted-foreground">Availability:</span>
                    <Badge 
                      variant="outline" 
                      className={`text-xs ${
                        product.availability === 'in-stock' 
                          ? 'text-green-700 dark:text-green-300' 
                          : product.availability === 'limited'
                          ? 'text-yellow-700 dark:text-yellow-300'
                          : 'text-red-700 dark:text-red-300'
                      }`}
                    >
                      {product.availability.replace('-', ' ')}
                    </Badge>
                  </div>
                </div>

                {/* Tags */}
                {product.tags.length > 0 && (
                  <div className="space-y-2">
                    <h4 className="font-medium text-sm">Tags</h4>
                    <div className="flex flex-wrap gap-1">
                      {product.tags.map((tag) => (
                        <Badge key={tag} variant="outline" className="text-xs">
                          {tag}
                        </Badge>
                      ))}
                    </div>
                  </div>
                )}
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Comparison Summary */}
        <Card className="mt-6">
          <CardHeader>
            <CardTitle className="text-lg">Comparison Summary</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
              <div className="space-y-1">
                <span className="font-medium">Best Price:</span>
                <p className="text-muted-foreground">
                  {products.find(p => p.price === bestPrice)?.name} - {getCurrencySymbol()}{convertPrice(bestPrice || 0).toFixed(0)}
                </p>
              </div>
              <div className="space-y-1">
                <span className="font-medium">Highest Rated:</span>
                <p className="text-muted-foreground">
                  {products.find(p => p.rating === bestRating)?.name} - {bestRating}★
                </p>
              </div>
              <div className="space-y-1">
                <span className="font-medium">Closest Store:</span>
                <p className="text-muted-foreground">
                  {products.reduce((closest, current) => 
                    current.store.distance < closest.store.distance ? current : closest
                  ).store.name} - {Math.min(...products.map(p => p.store.distance))} km
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </DialogContent>
    </Dialog>
  );
}
