'use client';

import { useState, useMemo, useCallback } from 'react';
import { Product, FilterOptions } from '@/types/indian-product';
import { indianProducts, indianCities } from '@/lib/indian-mock-data';

const initialFilters: FilterOptions = {
  query: '',
  category: '',
  city: '',
  priceRange: [0, 1000],
  minRating: 0,
  maxDistance: 50,
  sortBy: 'name',
  sortOrder: 'asc'
};

export function useIndianProducts() {
  const [filters, setFilters] = useState<FilterOptions>(initialFilters);
  const [selectedProducts, setSelectedProducts] = useState<string[]>([]);
  const [isComparisonOpen, setIsComparisonOpen] = useState(false);
  const [currency, setCurrency] = useState<'INR' | 'USD'>('INR');
  const [user, setUser] = useState(() => {
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('user');
      return saved ? JSON.parse(saved) : null;
    }
    return null;
  });

  const currencyRate = 0.012; // 1 INR = 0.012 USD

  const convertPrice = (price: number) => {
    if (currency === 'USD') {
      return price * currencyRate;
    }
    return price;
  };

  const getCurrencySymbol = () => {
    return currency === 'INR' ? '₹' : '$';
  };

  // Filter and sort products based on current filters
  const filteredProducts = useMemo(() => {
    let filtered = indianProducts.filter((product) => {
      // Text search
      if (filters.query && !product.name.toLowerCase().includes(filters.query.toLowerCase()) &&
          !product.description.toLowerCase().includes(filters.query.toLowerCase()) &&
          !product.tags.some(tag => tag.toLowerCase().includes(filters.query.toLowerCase()))) {
        return false;
      }

      // Category filter
      if (filters.category && product.category !== filters.category) {
        return false;
      }

      // City filter
      if (filters.city && product.store.city !== filters.city) {
        return false;
      }

      // Price range filter
      if (product.price < filters.priceRange[0] || product.price > filters.priceRange[1]) {
        return false;
      }

      // Rating filter
      if (product.rating < filters.minRating) {
        return false;
      }

      // Distance filter
      if (product.store.distance > filters.maxDistance) {
        return false;
      }

      return true;
    });

    // Sort products
    filtered.sort((a, b) => {
      let comparison = 0;
      
      switch (filters.sortBy) {
        case 'price':
          comparison = a.price - b.price;
          break;
        case 'rating':
          comparison = a.rating - b.rating;
          break;
        case 'distance':
          comparison = a.store.distance - b.store.distance;
          break;
        case 'name':
        default:
          comparison = a.name.localeCompare(b.name);
          break;
      }

      return filters.sortOrder === 'desc' ? -comparison : comparison;
    });

    return filtered;
  }, [filters]);

  // Get products selected for comparison
  const comparisonProducts = useMemo(() => {
    return selectedProducts
      .map(id => indianProducts.find(p => p.id === id))
      .filter(Boolean) as Product[];
  }, [selectedProducts]);

  // Update filters
  const updateFilters = useCallback((newFilters: Partial<FilterOptions>) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
  }, []);

  // Reset filters
  const resetFilters = useCallback(() => {
    setFilters(initialFilters);
  }, []);

  // Toggle product selection for comparison
  const toggleProductSelection = useCallback((productId: string) => {
    setSelectedProducts(prev => {
      if (prev.includes(productId)) {
        return prev.filter(id => id !== productId);
      } else if (prev.length < 3) { // Limit to 3 products for comparison
        return [...prev, productId];
      }
      return prev;
    });
  }, []);

  // Clear all selected products
  const clearSelection = useCallback(() => {
    setSelectedProducts([]);
  }, []);

  // Check if product is selected
  const isProductSelected = useCallback((productId: string) => {
    return selectedProducts.includes(productId);
  }, [selectedProducts]);

  // Open/close comparison modal
  const openComparison = useCallback(() => {
    if (selectedProducts.length >= 2) {
      setIsComparisonOpen(true);
    }
  }, [selectedProducts.length]);

  const closeComparison = useCallback(() => {
    setIsComparisonOpen(false);
  }, []);

  // Get best deals (products marked as best deals)
  const bestDeals = useMemo(() => {
    return filteredProducts.filter(product => product.isBestDeal);
  }, [filteredProducts]);

  // Get featured products (highest rated with good reviews)
  const featuredProducts = useMemo(() => {
    return filteredProducts
      .filter(product => product.rating >= 4.5 && product.reviewCount >= 100)
      .slice(0, 6);
  }, [filteredProducts]);

  // Get nearby products (within 5km)
  const nearbyProducts = useMemo(() => {
    return filteredProducts.filter(product => product.store.distance <= 5);
  }, [filteredProducts]);

  const logout = () => {
    localStorage.removeItem('user');
    setUser(null);
  };

  return {
    // Data
    products: filteredProducts,
    comparisonProducts,
    bestDeals,
    featuredProducts,
    nearbyProducts,
    cities: indianCities,
    categories: ['fruits', 'vegetables', 'restaurants', 'groceries', 'spices', 'sweets'],
    
    // State
    filters,
    selectedProducts,
    isComparisonOpen,
    currency,
    user,
    
    // Actions
    updateFilters,
    resetFilters,
    toggleProductSelection,
    clearSelection,
    isProductSelected,
    openComparison,
    closeComparison,
    setCurrency,
    logout,
    
    // Computed values
    hasFilters: Object.values(filters).some((value, index) => {
      const initialValue = Object.values(initialFilters)[index];
      return JSON.stringify(value) !== JSON.stringify(initialValue);
    }),
    canCompare: selectedProducts.length >= 2,
    selectionCount: selectedProducts.length,
    convertPrice,
    getCurrencySymbol,
    currencyRate
  };
}
