'use client';

import { useState } from 'react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Slider } from '@/components/ui/slider';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { FilterOptions } from '@/types/indian-product';

interface IndianSearchFiltersProps {
  filters: FilterOptions;
  onFiltersChange: (filters: Partial<FilterOptions>) => void;
  onReset: () => void;
  hasFilters: boolean;
  resultCount: number;
  cities: string[];
  categories: string[];
  currency: 'INR' | 'USD';
  convertPrice: (price: number) => number;
  getCurrencySymbol: () => string;
}

export function IndianSearchFilters({
  filters,
  onFiltersChange,
  onReset,
  hasFilters,
  resultCount,
  cities,
  categories,
  currency,
  convertPrice,
  getCurrencySymbol
}: IndianSearchFiltersProps) {
  const [showAdvanced, setShowAdvanced] = useState(false);

  return (
    <div className="space-y-6">
      {/* Main Search Bar */}
      <Card>
        <CardContent className="pt-6">
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="flex-1">
              <Input
                placeholder="Search Indian products, restaurants, or stores..."
                value={filters.query}
                onChange={(e) => onFiltersChange({ query: e.target.value })}
                className="w-full"
              />
            </div>
            <div className="flex gap-2">
              <Select
                value={filters.city}
                onValueChange={(value) => onFiltersChange({ city: value === 'all' ? '' : value })}
              >
                <SelectTrigger className="w-40">
                  <SelectValue placeholder="Select City" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Cities</SelectItem>
                  {cities.map((city) => (
                    <SelectItem key={city} value={city}>
                      {city}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <Button
                variant="outline"
                onClick={() => setShowAdvanced(!showAdvanced)}
                className="whitespace-nowrap"
              >
                {showAdvanced ? 'Hide' : 'More'} Filters
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Advanced Filters */}
      {showAdvanced && (
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Advanced Filters</CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {/* Category Filter */}
              <div className="space-y-2">
                <label className="text-sm font-medium">Category</label>
                <Select
                  value={filters.category}
                  onValueChange={(value) => onFiltersChange({ category: value === 'all' ? '' : value })}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="All Categories" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Categories</SelectItem>
                    {categories.map((category) => (
                      <SelectItem key={category} value={category}>
                        {category.charAt(0).toUpperCase() + category.slice(1)}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              {/* Sort Options */}
              <div className="space-y-2">
                <label className="text-sm font-medium">Sort By</label>
                <div className="flex gap-2">
                  <Select
                    value={filters.sortBy}
                    onValueChange={(value: any) => onFiltersChange({ sortBy: value })}
                  >
                    <SelectTrigger className="flex-1">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="name">Name</SelectItem>
                      <SelectItem value="price">Price</SelectItem>
                      <SelectItem value="rating">Rating</SelectItem>
                      <SelectItem value="distance">Distance</SelectItem>
                    </SelectContent>
                  </Select>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => onFiltersChange({ 
                      sortOrder: filters.sortOrder === 'asc' ? 'desc' : 'asc' 
                    })}
                    className="px-3"
                  >
                    {filters.sortOrder === 'asc' ? '↑' : '↓'}
                  </Button>
                </div>
              </div>

              {/* Minimum Rating */}
              <div className="space-y-2">
                <label className="text-sm font-medium">Minimum Rating</label>
                <Select
                  value={filters.minRating.toString()}
                  onValueChange={(value) => onFiltersChange({ minRating: parseFloat(value) })}
                >
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="0">Any Rating</SelectItem>
                    <SelectItem value="3">3+ Stars</SelectItem>
                    <SelectItem value="4">4+ Stars</SelectItem>
                    <SelectItem value="4.5">4.5+ Stars</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>

            {/* Price Range */}
            <div className="space-y-3">
              <label className="text-sm font-medium">
                Price Range: {getCurrencySymbol()}{convertPrice(filters.priceRange[0]).toFixed(0)} - {getCurrencySymbol()}{convertPrice(filters.priceRange[1]).toFixed(0)}
              </label>
              <Slider
                value={filters.priceRange}
                onValueChange={(value) => onFiltersChange({ priceRange: value as [number, number] })}
                max={1000}
                min={0}
                step={10}
                className="w-full"
              />
            </div>

            {/* Distance Filter */}
            <div className="space-y-3">
              <label className="text-sm font-medium">
                Maximum Distance: {filters.maxDistance} km
              </label>
              <Slider
                value={[filters.maxDistance]}
                onValueChange={(value) => onFiltersChange({ maxDistance: value[0] })}
                max={50}
                min={1}
                step={1}
                className="w-full"
              />
            </div>
          </CardContent>
        </Card>
      )}

      {/* Filter Summary */}
      <div className="flex flex-wrap items-center justify-between gap-4">
        <div className="flex flex-wrap items-center gap-2">
          <span className="text-sm text-muted-foreground">
            {resultCount} results found
          </span>
          {hasFilters && (
            <>
              <span className="text-sm text-muted-foreground">•</span>
              <Button
                variant="ghost"
                size="sm"
                onClick={onReset}
                className="h-6 px-2 text-xs"
              >
                Clear all filters
              </Button>
            </>
          )}
        </div>

        {/* Active Filters */}
        <div className="flex flex-wrap gap-2">
          {filters.query && (
            <Badge variant="secondary" className="text-xs">
              Search: "{filters.query}"
            </Badge>
          )}
          {filters.category && (
            <Badge variant="secondary" className="text-xs">
              {filters.category.charAt(0).toUpperCase() + filters.category.slice(1)}
            </Badge>
          )}
          {filters.city && (
            <Badge variant="secondary" className="text-xs">
              {filters.city}
            </Badge>
          )}
          {filters.minRating > 0 && (
            <Badge variant="secondary" className="text-xs">
              {filters.minRating}+ Stars
            </Badge>
          )}
        </div>
      </div>
    </div>
  );
}
