'use client';

import { Product } from '@/types/indian-product';
import { Card, CardContent, CardFooter, CardHeader } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import Image from 'next/image';

interface IndianProductCardProps {
  product: Product;
  isSelected: boolean;
  onToggleSelection: (productId: string) => void;
  onViewDetails?: (product: Product) => void;
  currency: 'INR' | 'USD';
  convertPrice: (price: number) => number;
  getCurrencySymbol: () => string;
}

export function IndianProductCard({
  product,
  isSelected,
  onToggleSelection,
  onViewDetails,
  currency,
  convertPrice,
  getCurrencySymbol
}: IndianProductCardProps) {
  const discountPercentage = product.originalPrice
    ? Math.round(((product.originalPrice - product.price) / product.originalPrice) * 100)
    : 0;

  const getAvailabilityColor = (availability: string) => {
    switch (availability) {
      case 'in-stock':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200';
      case 'limited':
        return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200';
      case 'out-of-stock':
        return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200';
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200';
    }
  };

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'fruits':
        return 'bg-orange-100 text-orange-800 dark:bg-orange-900 dark:text-orange-200';
      case 'vegetables':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200';
      case 'restaurants':
        return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200';
      case 'groceries':
        return 'bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200';
      case 'spices':
        return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200';
      case 'sweets':
        return 'bg-pink-100 text-pink-800 dark:bg-pink-900 dark:text-pink-200';
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200';
    }
  };

  return (
    <Card className={`group relative transition-all duration-200 hover:shadow-lg ${
      isSelected ? 'ring-2 ring-primary' : ''
    } ${product.isBestDeal ? 'border-primary/50' : ''}`}>
      {/* Best Deal Indicator */}
      {product.isBestDeal && (
        <div className="absolute -top-2 -right-2 z-10">
          <Badge className="bg-primary text-primary-foreground text-xs px-2 py-1">
            Best Deal
          </Badge>
        </div>
      )}

      {/* Product Image */}
      {product.image && (
        <div className="relative w-full h-48 overflow-hidden rounded-t-lg">
          <Image
            src={product.image}
            alt={product.name}
            fill
            className="object-cover transition-transform duration-200 group-hover:scale-105"
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
          />
          <div className="absolute inset-0 bg-gradient-to-t from-black/20 to-transparent" />
        </div>
      )}

      <CardHeader className="pb-3">
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <h3 className="font-semibold text-lg leading-tight mb-1">
              {product.name}
            </h3>
            <div className="flex items-center gap-2 mb-2">
              <Badge 
                variant="secondary" 
                className={`text-xs ${getCategoryColor(product.category)}`}
              >
                {product.category.charAt(0).toUpperCase() + product.category.slice(1)}
              </Badge>
              <Badge 
                variant="outline" 
                className={`text-xs ${getAvailabilityColor(product.availability)}`}
              >
                {product.availability.replace('-', ' ')}
              </Badge>
            </div>
          </div>
        </div>

        {/* Price Section */}
        <div className="flex items-center gap-2">
          <span className="text-2xl font-bold text-primary">
            {getCurrencySymbol()}{convertPrice(product.price).toFixed(0)}
          </span>
          {product.originalPrice && (
            <>
              <span className="text-sm text-muted-foreground line-through">
                {getCurrencySymbol()}{convertPrice(product.originalPrice).toFixed(0)}
              </span>
              <Badge variant="destructive" className="text-xs">
                -{discountPercentage}%
              </Badge>
            </>
          )}
        </div>
      </CardHeader>

      <CardContent className="pb-3">
        <p className="text-sm text-muted-foreground mb-3 line-clamp-2">
          {product.description}
        </p>

        <Separator className="my-3" />

        {/* Store Information */}
        <div className="space-y-2">
          <div className="flex items-center justify-between text-sm">
            <span className="font-medium">{product.store.name}</span>
            <span className="text-muted-foreground">{product.store.distance} km</span>
          </div>
          <div className="text-xs text-muted-foreground">
            {product.store.address}, {product.store.city}
          </div>
        </div>

        <Separator className="my-3" />

        {/* Rating and Reviews */}
        <div className="flex items-center justify-between text-sm">
          <div className="flex items-center gap-1">
            <span className="font-medium">{product.rating}</span>
            <span className="text-yellow-500">★</span>
            <span className="text-muted-foreground">
              ({product.reviewCount} reviews)
            </span>
          </div>
          <div className="text-xs text-muted-foreground">
            Store: {product.store.rating}★
          </div>
        </div>

        {/* Tags */}
        {product.tags.length > 0 && (
          <div className="flex flex-wrap gap-1 mt-3">
            {product.tags.slice(0, 3).map((tag) => (
              <Badge key={tag} variant="outline" className="text-xs">
                {tag}
              </Badge>
            ))}
            {product.tags.length > 3 && (
              <Badge variant="outline" className="text-xs">
                +{product.tags.length - 3}
              </Badge>
            )}
          </div>
        )}
      </CardContent>

      <CardFooter className="pt-3 flex gap-2">
        <Button
          variant={isSelected ? "default" : "outline"}
          size="sm"
          onClick={() => onToggleSelection(product.id)}
          className="flex-1"
        >
          {isSelected ? 'Selected' : 'Compare'}
        </Button>
        {onViewDetails && (
          <Button
            variant="ghost"
            size="sm"
            onClick={() => onViewDetails(product)}
            className="flex-1"
          >
            Details
          </Button>
        )}
      </CardFooter>
    </Card>
  );
}
